.header{
	padding: 5px 20px;
	display: flex;
	justify-content: space-between;
	position: sticky;
	background-color: white;
	z-index: 100;
	top: 0;
	box-shadow: 0px 5px 8px -9px rgba(0,0,0,0.75);
	
}

/*

The z-index property specifies the stack order of an element.

An element with greater stack order is always in front of an element with a lower stack order.

Note: z-index only works on positioned elements (position: absolute, position: relative, position: fixed, or position: sticky).

*/

.header__left{
	display: flex;
	justify-content: space-between;
}

.header__input{
	display: flex;
	align-items: center;
	background-color: #eff2f5;
	padding: 10px;
	margin-left: 10px;
	margin-top: 5px;
	margin-bottom: 5px;
	border-radius: 999px;
}

.header__left > img{
	height: 40px;
	margin: auto;
}

.header__input > input{
	border: none;
	background-color: transparent;
	outline-width: 0;
}

.header__center{
	display: flex;
	flex: 1;
	justify-content: center;
}

.header__option > .MuiSvgIcon-root{
	color: grey;
}

.header__option:hover > .MuiSvgIcon-root{
	color: #2e81f4;
}

.header__option--active > .MuiSvgIcon-root{
	color: #2e81f4;
}

.header__option--active{
	border-bottom: 4px solid #2e81f4;
}

.header__option{
	display: flex;
	align-items: center;
	padding: 0 30px;
	cursor: pointer;
}

.header__option:hover{
	background-color: #eff2f5;
	border-radius: 10px;
	align-items: center;
	padding: 0 30px;
	border-bottom: none;
}

.header__info{
	display: flex;		/* It will keep both the elements inside it into one line */
	align-items: center;
}

.header__info > h4{
	margin-left: 10px;
	margin-right: 10px;
}

.header__right{
	display: flex;	/* It will keep all the elements inside it into one line */
	margin-right: 15px;
}

.header__right--icons{
	background-color: #CFD8DC;
	border-radius: 50px;
	margin: 1%;
}